<template>
  <div>
    <br />
    <br />
    <div id="chart2" width="500" height="700"></div>
    <br />
  </div>
</template>

<script scoped>
import embed from 'vega-embed'
export default {
  name: 'Chart2',
  mounted() {
    console.log('Loaded')
    this.loadChart()
  },
  methods: {
    async loadChart() {
      var spec = {
        width: 1000,
        height: 500,
        $schema: 'https://vega.github.io/schema/vega-lite/v5.json',
        description: 'A piechart that shows number of departures in 2019',
        title: 'A piechart that shows number of departures in 2019',
        data: {
          values: [
            { country: 'Germany', value: 99533000 },
            { country: 'United States', value: 170930094 },
            { country: 'Poland', value: 50600000 },
            { country: 'Canada', value: 37845898 },
            { country: 'Mexico', value: 82752000 },
          ],
        },
        mark: { type: 'arc', tooltip: true },
        encoding: {
          theta: { field: 'value', type: 'quantitative', stack: 'normalize' },
          color: { field: 'country', type: 'nominal' },
        },
        config: {},
      }
      embed('#chart2', spec)
        // result.view provides access to the Vega View API
        .then((result) => console.log(result))
        .catch(console.warn)
    },
  },
}
</script>

