<template>
  <div>
    <svg
      id="symbol-chart"
      width="1200"
      height="610"
      style="padding: 30px;"
    ></svg>
  </div>
</template>

<script>
import * as d3 from 'd3'
import * as topojson from 'topojson-client'
export default {
  name: 'Chart8',
  mounted() {
    console.log('Loaded')
    this.loadChart()
  },
  methods: {
    async loadChart() {
      var promises = []
      var files = ['data/countries-50m.topojson', 'data/airport.json']
      files.forEach((url) => promises.push(d3.json(url)))
      Promise.all(promises).then(function (values) {
        var us = values[0]
        var data = values[1]

        var format = d3.format(',.0f')

        data = new Map(data.slice(1).map(([gdp, country]) => [country, +gdp]))

        var radius = d3.scaleSqrt(
          [0, d3.quantile([...data.values()].sort(d3.ascending), 0.985)],
          [0, 20],
        )

        var svg = d3.select('#symbol-chart').attr('viewBox', [0, 0, 1200, 610])

        var projection = d3.geoMercator(200)
        var path = d3.geoPath().projection(projection)

        svg
          .append('path')
          .datum(topojson.feature(us, us.objects.countries))
          .attr('fill', '#ccc')
          .attr('d', path)

        svg
          .append('path')
          .datum(topojson.mesh(us, us.objects.countries, (a, b) => a !== b))
          .attr('fill', 'none')
          .attr('stroke', 'white')
          .attr('stroke-linejoin', 'round')
          .attr('d', path)

        const legend = svg
          .append('g')
          .attr('fill', '#777')
          .attr('transform', 'translate(990,558)')
          .attr('text-anchor', 'middle')
          .style('font', '10px sans-serif')
          .selectAll('g')
          .data([1e3, 5e3, 1e4])
          .join('g')

        legend
          .append('circle')
          .attr('fill', 'none')
          .attr('stroke', '#ccc')
          .attr('cy', (d) => -radius(d) * 10)
          .attr('r', radius)

        legend
          .append('text')
          .attr('y', (d) => -2 * radius(d) * 5)
          .attr('dy', '1.3em')
          .text(d3.format('.1s'))

        svg
          .append('g')
          .attr('fill', 'brown')
          .attr('fill-opacity', 0.5)
          .attr('stroke', '#fff')
          .attr('stroke-width', 0.5)
          .selectAll('circle')
          .data(
            topojson
              .feature(us, us.objects.countries)
              .features.map((d) => ((d.value = data.get(d.id)), d))
              .sort((a, b) => b.value - a.value),
          )
          .join('circle')
          .attr('transform', (d) => `translate(${path.centroid(d)})`)
          .attr('r', (d) => radius(d.value))
          .append('title')
          .text((d) => `${d.properties.name}, ${format(d.value)}`)

        svg
          .append('text')
          .attr('x', 600)
          .attr('y', 450)
          .attr('text-anchor', 'middle')
          .style('font-size', '16px')
          .text(
            'Covid Impact reflected by numbers change for various countries as compared to 2019',
          )
      })
    },
  },
}
</script>

