          .selectAll('.name')
          .delay(delay)
          .attr('x', (d) => x(d.name) + x.bandwidth() / 2)
      }

      function draw() {
        // console.log(current);
        x.domain(current.map((d) => d.name))
          .range([0, width])
          .paddingInner(0.2)

        y.domain([0, d3.max(current, (d) => d.Total)]).range([height, 0])

        svg
          .selectAll('.bar')
          .data(current, (d) => d.name)
          .enter()
          .append('rect')
          .attr('class', 'bar')
          .attr('x', (d) => x(d.name))
          .attr('y', (d) => y(d.Total))
          .attr('width', x.bandwidth())
          .attr('height', (d) => height - y(d.Total))

        // svg.selectAll('.name')
        //   .data(current, d => d.Country)
        //   .enter()
        //   .append('text')
        //   .text(d => d.Country)
        //   .attr('class', 'name')
        //   .attr('x', d => x(d.Country) + x.bandwidth() / 2)
        //   .attr('y', d => y(d.Total) + (height - y(d.Total)) / 2);

        //var xAxis;
        xAxis = d3.axisBottom().scale(x)
        //.ticks(0)
        //.tickSize(0)
        //.tickFormat('');

        svg
          .append('g')
          .attr('id', 'x-axis')
          .attr('class', 'axis')
          .attr('transform', 'translate(0,' + height + ')')
          .call(xAxis)

        svg
          .append('text')
          .attr('x', width / 2)
          .attr('y', height + 30)
          .attr('class', 'label')
          .attr('id', 'x_label')
          .text('Top 20 Airports') // update the label based on sort mode

        yAxis = d3.axisLeft().scale(y).ticks(10, 'd')

        svg.append('g').attr('id', 'y-axis').attr('class', 'axis').call(yAxis)

        svg
          .append('text')
          .attr('x', -height / 2)
          .attr('y', -margin.left * 0.7)
          .attr('transform', 'rotate(-90)')
          .attr('class', 'label')
          .append('tspan')
          .text('Traffic in Airports')
      }
    },
  },
}
</script>

