<template>
  <div>
    <br />
    <p>
      Multi-Line Chart showing the top 10 countries for maximum number of
      incoming tourists over a period of 14 years
    </p>
    <svg id="chart4" width="1000" height="600" viewBox="0 0 975 610"></svg>
  </div>
</template>

<script>
import * as d3 from 'd3'
export default {
  name: 'Chart4',
  mounted() {
    this.loadChart()
  },
  methods: {
    async loadChart() {
      // Set the dimensions of the canvas / graph
      var margin = { top: 30, right: 20, bottom: 70, left: 50 },
        width = 900 - margin.left - margin.right,
        height = 500 - margin.top - margin.bottom

      // Set the ranges
      var x = d3.scaleTime().range([0, width - 175])
      var y = d3.scaleLinear().range([height, 0])

      var parseDate = d3.timeParse('%Y')
      // Define the line
      var priceline = d3
        .line()
        .x(function (d) {
          return x(d.date)
        })
        .y(function (d) {
          return y(d.price)
        })

      // Adds the svg canvas
      var svg = d3
        .select('#chart4')
        .append('svg')
        .attr('width', width + margin.left + margin.right)
        .attr('height', height + margin.top + margin.bottom)
        .append('g')
        .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')')

      // Get the data
      d3.csv('data/Tourism_line.csv').then(function (data) {
        data.forEach(function (d) {
          console.log(d)
          d.date = parseDate(d.Year)
          d.price = +d.Value
        })

        // Scale the range of the data
        x.domain(
          d3.extent(data, function (d) {
            return d.date
          }),
        )
        y.domain([
          0,
          d3.max(data, function (d) {
            return d.price
          }),
        ])

        var dataNest = []
        // Group the entries by Country
        dataNest = Array.from(
          d3.group(data, (d) => d.Country),
          ([key, value]) => ({ key, value }),
        )

        // set the colour scale
        var color = d3.scaleOrdinal(d3.schemeCategory10)

        var legendSpace = height / dataNest.length // spacing for the legend

        // Loop through each Country / key
        dataNest.forEach(function (d, i) {
          svg
            .append('path')
            .attr('class', 'line')
            .style('stroke', function () {
              // Add the colours dynamically
              return (d.color = color(d.key))
            })
            .attr('id', 'tag' + d.key.replace(/\s+/g, '')) // assign an ID
            .attr('d', priceline(d.value))

          // Add the Legend
          svg
            .append('text')
            .attr('x', width - 75) // space legend
            .attr('y', legendSpace / 2 + i * legendSpace)
            .attr('class', 'legend') // style the legend
            .style('fill', function () {
              // Add the colours dynamically
              return (d.color = color(d.key))
            })
            .on('click', function () {
              // Determine if current line is visible
              var active = d.active ? false : true,
                newOpacity = active ? 0 : 1
              // Hide or show the elements based on the ID
              d3.select('#tag' + d.key.replace(/\s+/g, ''))
                .transition()
                .duration(100)
                .style('opacity', newOpacity)
              // Update whether or not the elements are active
              d.active = active
            })
            .text(d.key)
        })

        // Add the X Axis
        svg
          .append('g')
          .attr('class', 'axis')
          .attr('transform', 'translate(0,' + height + ')')
          .call(d3.axisBottom(x))

        // Add the Y Axis
        svg.append('g').attr('class', 'axis').call(d3.axisLeft(y))

        svg
          .append('text')
          .attr('x', 300)
          .attr('y', 435)
          .text('Years')
          .style('font-size', '10px')

        svg
          .append('text')
          .attr('x', -300)
          .attr('y', -33)
          .text('Number of tourist (x1000)')
          .style('font-size', '10px')
          .attr('transform', 'translate(-10,0)rotate(-90)')
      })
    },
  },
}
</script>

<style>
/* set the CSS */

#chart4 body {
  font: 10px Arial;
}

#chart4 path {
  stroke: steelblue;
  stroke-width: 2;
  fill: none;
}

#chart4 .axis path,
#chart4 .axis line {
  fill: none;
  stroke: grey;
  stroke-width: 1;
  shape-rendering: crispEdges;
}

#chart4 .legend {
  font-size: 16px;
  font-weight: bold;
  text-anchor: middle;
}
</style>
