<template>
  <div id="map">
    <h3>International Connections from US Airports</h3>
    <label>Month</label>
    &nbsp;
    <input type="range" name="month" id="monthsl" min="1" max="12" value="1" />
    &nbsp;
    <output id="opmonthsl">1</output>
    <br />
    <label>Year</label>
    &nbsp;
    <input
      type="range"
      name="year"
      id="yearsl"
      min="1990"
      max="2019"
      value="1990"
    />
    &nbsp;
    <output id="opyearsl">1990</output>
    <br />
    <label>Airport</label>
    &nbsp;
    <select id="airportl">
      <option value="LAX">LAX</option>
      <option value="JFK">JFK</option>
      <option value="MIA">MIA</option>
      <option value="IAH">IAH</option>
      <option value="ATL">ATL</option>
      <option value="DFW">DFW</option>
      <option value="FLL">FLL</option>
      <option value="BOS">BOS</option>
      <option value="DEN">DEN</option>
      <option value="LGA">LGA</option>
      <option value="PHL">FLL</option>
    </select>
    <output id="opairportl"></output>
    <br />
    <svg id="my_dataviz" width="1000" height="600" viewBox="0 0 975 610"></svg>
  </div>
</template>

<script>
import * as d3 from 'd3'
import * as topojson from 'topojson-client'

export default {
  name: 'Chart1',
  mounted() {
    console.log('Loaded')
    this.loadChart()
  },
  methods: {
    async loadChart() {
      const svg = d3.select('#my_dataviz')

      let projection1 = d3.geoMercator()

      let path1 = d3.geoPath().projection(projection1)

      Promise.all([
        d3.json('data/countries-50m.topojson'), // World shape
        d3.csv('data/flight_connection1.csv'), // Position of circles
      ]).then(function (initialize) {
        let us = initialize[0]
        let data = initialize[1]

        var data1 = data.filter(function (d) {
          if (d['Month'] == 1 && d['Year'] == 1990 && d['usg_apt'] == 'LAX') {
            return d
          }
        })

        // console.log(data1);

        // Reformat the list of link. Note that columns in csv file are called long1, long2, lat1, lat2
        const link = []
        let source = []
        let target = []
        let topush = {}
        data1.forEach(function (row) {
          source = [+row.lon_x, +row.lat_x]
          target = [+row.lon_y, +row.lat_y]
          topush = {
            coord: { type: 'LineString', coordinates: [source, target] },
            us_apt: row.usg_apt,
            fg_apt: row.fg_apt,
          }
          link.push(topush)
        })

        console.log(link)

        // Draw the map
        let path = d3.geoPath().projection(d3.geoMercator())

        //add the geopath for all the states
        svg
          .append('path')
          .datum(topojson.feature(us, us.objects.countries))
          .attr('fill', '#ccc')
          .attr('d', path)

        //add the geopath for the borders of the states
        svg
          .append('path')
          .datum(topojson.mesh(us, us.objects.countries, (a, b) => a !== b))
          .attr('fill', 'none')
          .attr('stroke', 'white')
          .attr('stroke-linejoin', 'round')
          .attr('d', path)

        // Add the path
        svg
          .selectAll('myPath')
          .data(link)
          .join('path')
          .attr('d', function (d) {
            return path1(d.coord)
          })
          .attr('class', 'connection')
          .attr('id', 'conn')
          .style('fill', 'none')
          .style('stroke', '#69b3a2')
          .style('stroke-width', 2)
          .on('mouseover', function () {
            d3.select(this)
              .style('stroke', 'red')
              .style('stroke-width', 3)
              .append('title')
              .text((d) => `${d.us_apt} ${d.fg_apt}`)
          })
          .on('mouseout', function () {
            d3.select(this).style('stroke', '#69b3a2').style('stroke-width', 2)
          })
      })

      function updateChart(m, y, ap) {
        d3.select('#my_dataviz').selectAll('.connection').remove()

        let path1 = d3.geoPath().projection(projection1)

        console.log(m, y)

        Promise.all([d3.csv('data/flight_connection1.csv')]).then(function (
          initialize,
        ) {
          let data = initialize[0]

          var data1 = data.filter(function (d) {
            if (d['Month'] == m && d['Year'] == y && d['usg_apt'] == ap) {
              return d
            }
          })

          // Reformat the list of link. Note that columns in csv file are called long1, long2, lat1, lat2
          const link = []
          let source = []
          let target = []
          let topush = {}
          data1.forEach(function (row) {
            source = [+row.lon_x, +row.lat_x]
            target = [+row.lon_y, +row.lat_y]
            topush = {
              coord: { type: 'LineString', coordinates: [source, target] },
              us_apt: row.usg_apt,
              fg_apt: row.fg_apt,
            }
            link.push(topush)
          })

          console.log(link)

          var bars = svg.selectAll('myPath').data(link, (d) => d.us_apt)

          // ENTER.
          bars
            .enter()
            .append('path')
            .attr('d', function (d) {
              return path1(d.coord)
            })
            .attr('class', 'connection')
            .attr('id', 'conn')
            .style('fill', 'none')
            .style('stroke', '#69b3a2')
            .style('stroke-width', 2)
            .style('opacity', 0)
            .on('mouseover', function () {
              d3.select(this)
                .style('stroke', 'red')
                .style('stroke-width', 3)
                .append('title')
                .text((d) => `${d.us_apt} ${d.fg_apt}`)
            })
            .on('mouseout', function () {
              d3.select(this)
                .style('stroke', '#69b3a2')
                .style('stroke-width', 2)
            })
            .transition()
            .duration(100)
            .attr('d', function (d) {
              return path1(d.coord)
            })
            .attr('class', 'connection')
            .attr('id', 'conn')
            .style('fill', 'none')
            .style('stroke', '#69b3a2')
            .style('stroke-width', 2)
            .style('opacity', 1)
            .on('mouseover', function () {
              d3.select(this)
                .style('stroke', 'red')
                .style('stroke-width', 3)
                .append('title')
                .text((d) => `${d.us_apt} ${d.fg_apt}`)
            })
            .on('mouseout', function () {
              d3.select(this)
                .style('stroke', '#69b3a2')
                .style('stroke-width', 2)
            })

          // EXIT.
          bars.exit().transition().duration(100).style('opacity', 0).remove()
        })
      }

      d3.select('#monthsl').on('change', function () {
        let month = this.value
        let year = d3.select('#yearsl').node().value
        let airport = d3.select('#airportl').node().value

        document.getElementById('opyearsl').innerHTML = year
        document.getElementById('opmonthsl').innerHTML = month
        updateChart(month, year, airport)
      })

      d3.select('#yearsl').on('change', function () {
        let month = d3.select('#monthsl').node().value
        let year = this.value
        let airport = d3.select('#airportl').node().value

        document.getElementById('opyearsl').innerHTML = year
        document.getElementById('opmonthsl').innerHTML = month
        updateChart(month, year, airport)
      })

      d3.select('#airportl').on('change', function () {
        let month = d3.select('#monthsl').node().value
        let year = d3.select('#yearsl').node().value
        let airport = this.value

        document.getElementById('opyearsl').innerHTML = year
        document.getElementById('opmonthsl').innerHTML = month
        updateChart(month, year, airport)
      })
    },
  },
}
</script>

<style scoped>
#map {
  padding: 20px;
  margin: 10px;
}
</style>
